import React,{createContext,useState} from 'react'

export let DataContext = createContext()

export let DataProvider = props => {

	let [data,setData] = useState({
		dataDisplay:{
			title:'The Employee List',
			rows:[
				["Joe James", "Test Corp", "Yonkers", "NY"],
				["John Walsh", "Test Corp", "Hartford", "CT"],
				["Bob Herm", "Test Corp", "Tampa", "FL"],
				["James Houston", "Test Corp", "Dallas", "TX"],
			],
			columns:["Name", "Company", "City", "State"]
		},
		stats:{
			problemType:'classification',
			numSamples:116,
			numFeatures:13,
			numTarget:1,
			featureStats:[{
				featureName:'calorie',
				central:3,
				type:'Nominal',
				nullValCount:20
				},{
				featureName:'ATP',
				central:1,
				type:'Interal',
				nullValCount:0
			}]
		},
		featureImportances:[{
				featureName:'Calorie',
				linear:1,
				tree:2,
				statistical:1
			},
			{
				featureName:'ATP',
				linear:3,
				tree:4,
				statistical:3
			},
			{
				featureName:'Calcium',
				linear:5,
				tree:5,
				statistical:5
			}],
		plotData:{
			problemType:'regression',
			x:[1,10,2],
			y:[3,16,9],
			xy:[{x:1,y:3}, {x:10,y:16}, {x:2,y:9},]
		},
		model_info:{
			best_model_name:'RandomForestRegressor',
			accuracy:'99.32'
		}	
	})

	return(
		<DataContext.Provider value={{data,setData}}>
			{props.children}
		</DataContext.Provider>
		)
}