import React,{useState,useContext} from 'react'
import { withStyles, makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import {DataContext} from '../../Context/Context'

const useStyles = makeStyles({
  table: {
    minWidth: 650,
  },
});

const StyledTableCell = withStyles((theme) => ({
  head: {
    backgroundColor: '#084c61' ,
    color: '#BCE7FD',
  },
  body: {
    fontSize: 14,
  },
}))(TableCell);

const StyledTableRow = withStyles((theme) => ({
  root: {
    '&:nth-of-type(even)': {
      backgroundColor: '#f4f4f4'
    },
  },
}))(TableRow);

let Stats = () => {

	let {data} = useContext(DataContext)

	const classes = useStyles()

	return(
		<div className='tabElement'>
      	<Typography variant="h2" gutterBottom>
        		Feature Importance
      	</Typography>
      	<Typography variant="h8" gutterBottom>
        		This table shows the features as ranked by the respective method
      	</Typography>
      	<div style={{marginTop:'30px'}}>
      		<TableContainer component={Paper}>
			      <Table className={classes.table} aria-label="simple table">
			        <TableHead>
			          <StyledTableRow>
			            <StyledTableCell>Feature Name</StyledTableCell>
			            <StyledTableCell>Tree</StyledTableCell>
			            <StyledTableCell>Statistical Test</StyledTableCell>
			          </StyledTableRow>
			        </TableHead>
			        <TableBody>
			          {data.featureImportances.map((row) => (
			            <StyledTableRow key={row.name}>
			              <StyledTableCell component="th" scope="row">
			                {row.featureName}
			              </StyledTableCell>
			              <StyledTableCell>{row.tree}</StyledTableCell>
			              <StyledTableCell>{row.statistical}</StyledTableCell>
			            </StyledTableRow>
			          ))}
			        </TableBody>
			      </Table>
			    </TableContainer>
			</div>    
      </div>
		)
}

export default Stats