import React,{useState,useContext} from 'react'
import './Main.css'
import Tabbed from '../Tabbed/Tabbed'
import IconButton from '@material-ui/core/IconButton';
import PhotoCamera from '@material-ui/icons/PhotoCamera';
import CloudUploadIcon from '@material-ui/icons/CloudUpload';
import axios from 'axios'
import {
  fade,
  ThemeProvider,
  withStyles,
  makeStyles,
  createMuiTheme,
} from '@material-ui/core/styles';
import InputBase from '@material-ui/core/InputBase';
import InputLabel from '@material-ui/core/InputLabel';
import TextField from '@material-ui/core/TextField';
import FormControl from '@material-ui/core/FormControl';
import { green } from '@material-ui/core/colors';
import Button from '@material-ui/core/Button';
import {DataContext} from '../../Context/Context'
import CheckIcon from '@material-ui/icons/Check';
import ReactLoading from "react-loading";
import Typography from '@material-ui/core/Typography';

const CssTextField = withStyles({
  root: {
  	color: '#BCE7FD',
    '& label.Mui-focused': {
      color: '#BCE7FD',
    },
    '& label':{
    	color: '#BCE7FD'
    },
    '& .MuiInput-underline:after': {
      borderBottomColor: '#BCE7FD',
    },
    '& .MuiOutlinedInput-root': {
      '& fieldset': {
        borderColor: '#BCE7FD',
        borderWidth:'2px'
      },
      '&:hover fieldset': {
        borderColor: '#BCE7FD',
      },
      '&.Mui-focused fieldset': {
        borderColor: '#BCE7FD',
      },
    },
  },
})(TextField);

const useStyles = makeStyles((theme) => ({
  root: {
    '& > *': {
      margin: theme.spacing(1),
    },
  },
  input: {
    display: 'none',
  },
  inputField:{
  	color:'#BCE7FD',
	fontSize:'14px',
	marginTop:'15px'
  },
  button:{
  	marginTop:'15px',
  	width:'100%',
  	backgroundColor:'#084c61',
  	color:'#BCE7FD',
  	'&:hover':{
  		backgroundColor:'#084c61',
  		color:'#BCE7FD',
  	}
  },
  fontColor:{
  	color:'#BCE7FD'
  }
}));

let Main = () => {

	let {setData} = useContext(DataContext)

	let [file,setFile] = useState(null)
	let [target,setTarget] = useState('')
	let [uploaded,setUploaded] = useState(false)
	let [isLoading,setIsLoading] = useState(false)
	let [show,setShow] = useState(false)

	let getInsights = (e) => {

		setIsLoading(true)

		let formdata = new FormData()
		formdata.append('file',file)
		formdata.append('target',target)

		console.log(file,target)
		axios({
		  	method: 'post',
		  	url: '/getData',
		  	headers: {
		  		'Access-Control-Allow-Origin': '*', 
		  		"Content-Type": "application/json" 
		  	},
		  	withCredentials:true,
		  	data:formdata
		})
		.then(res=>{
			setData(res.data)
			setIsLoading(false)
			setShow(true)
			console.log('In then')
		})
	}

	const classes = useStyles();

	return (
		<div
		className='window'>
			<div
			className={(show)?'main':'shrinkedMain'}>
				<div
				className='sidebar'>
					<h2>Data Insights.io</h2>
					<div style={{
						flexGrow:1,
						display:'flex',
						alignItems:'center',
						justifyContent:'center',
						flexDirection:'column',
						marginTop:'-20%'
					}}>
						<input
						onChange = {e=>{
							if(e.target.files){
								setUploaded(true)
								setFile(e.target.files[0])
							}
							else{
								setUploaded(false)
							}
						}} 
						accept="text/csv" className={classes.input} id="icon-button-file" type="file" />
				      <label htmlFor="icon-button-file">
				      	<IconButton style={{
						      background:'#BCE7FD'
						   }} 
						   aria-label="upload picture" component="span">
				      	{(uploaded==false)?
				      		(
						         <CloudUploadIcon />
				      		)
				      		:
				      		(
						         <CheckIcon />
				      		)
				      	}
				      	</IconButton>
				      </label>
				      <span style={{
				      	fontSize:'14px',
				      	marginTop:'5px'
				      }}> Allowed Format .csv</span>
				      <CssTextField
				      	style={{
				      		color: '#BCE7FD'
				      	}}
				      	InputProps={{
						    className: classes.fontColor
						  	}}	
						  	onChange={e=>{
						  		setTarget(e.target.value)
						  	}}
				        	className={classes.inputField}
				        	label="Target Name"
				        	variant="outlined"
				        	id="custom-css-outlined-input"
				      />
				      <Button onClick={getInsights}
				      elevation={0} variant="contained" className={classes.button}>
				        Get Insights
				      </Button>
			      </div>
				</div>
				<div
				className={(!show)?'none':'insights'}>
					<Tabbed/>
				</div>
				{(isLoading)?
					(
					<div style={{
						position:'fixed',
						height:'100vh',
						width:'100vw',
						display:'flex',
						alignItems:'center',
						justifyContent:'center',
						background:'#42033d',
						flexDirection:'column',
						top:0,
						left:0,
						zIndex:1000000
					}}>
						<h2 style={{color:'white',marginBottom:0}}>Data Insights.io</h2>
						<ReactLoading type={'cubes'} color="#fff" />
					</div>	
					):null
				}
			</div>
		</div>
		)
}

export default Main