import React,{useState,useContext} from 'react'
import { withStyles, makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import {DataContext} from '../../Context/Context'

const useStyles = makeStyles({
  table: {
    minWidth: 650,
  },
});

const StyledTableCell = withStyles((theme) => ({
  head: {
    backgroundColor: '#084c61' ,
    color: '#BCE7FD',
  },
  body: {
    fontSize: 14,
  },
}))(TableCell);

const StyledTableRow = withStyles((theme) => ({
  root: {
    '&:nth-of-type(even)': {
      backgroundColor: '#f4f4f4'
    },
  },
}))(TableRow);

let Stats = () => {

	let {data} = useContext(DataContext)

	const classes = useStyles()

	let cardStyle = {
		width:'27%',
		borderSizing:'border-box',
		borderRadius:'10px',
		padding:'20px',
		background:'#084c61',
		color:'#BCE7FD'
	}

	let rows = [{
		featureName:'calorie',
		central:3,
		type:'Nominal',
		nullValCount:20
	},{
		featureName:'ATP',
		central:1,
		type:'Interal',
		nullValCount:0
	}
	]

	return(
		<div className='tabElement'>
			<Typography variant="h2" gutterBottom>
        		{data.stats.problemType.charAt(0).toUpperCase() + data.stats.problemType.substring(1,data.stats.problemType.length)} Problem
      	</Typography>
      	<div style={{
      		display:'flex',
      		alignItems:'center',
      		justifyContent:'space-between'
      	}}>
	      	<div style={cardStyle}>
	      		<Typography variant='h3'>{data.stats.numSamples}</Typography>
	      		<Typography>Samples</Typography>
	      	</div>
	      	<div style={cardStyle}>
	      		<Typography variant='h3'>{data.stats.numFeatures}</Typography>
	      		<Typography>Features</Typography>
	      	</div>
	      	<div style={cardStyle}>
	      		<Typography variant='h3'>{data.stats.numTarget}</Typography>
	      		<Typography>Target</Typography>
	      	</div>
      	</div>

      	<div style={{
      		marginTop:'30px'
      	}}>
      	{/*<Typography variant="h4" gutterBottom>
      	        		Feature Stats
      	      	</Typography>
      	      	{(data.stats.featureStats.length!=0)?
      	      		(
      	      			<TableContainer component={Paper}>
      				      <Table className={classes.table} aria-label="simple table">
      				        <TableHead>
      				          <StyledTableRow>
      				            <StyledTableCell>Feature Name</StyledTableCell>
      				            <StyledTableCell>Type</StyledTableCell>
      				            <StyledTableCell>Central Tendency</StyledTableCell>
      				            <StyledTableCell>Null value count</StyledTableCell>
      				          </StyledTableRow>
      				        </TableHead>
      				        <TableBody>
      				          {data.stats.featureStats.map((row) => (
      				            <StyledTableRow key={row.featureName}>
      				              <StyledTableCell component="th" scope="row">
      				                {row.featureName}
      				              </StyledTableCell>
      				              <StyledTableCell>{row.type}</StyledTableCell>
      				              <StyledTableCell>{row.central}</StyledTableCell>
      				              <StyledTableCell>{row.nullValCount}</StyledTableCell>
      				            </StyledTableRow>
      				          ))}
      				        </TableBody>
      				      </Table>
      				    </TableContainer>
      	      		):null
      	      		}*/}
      	</div>

		</div>
		)
}

export default Stats