import React,{useState,useContext} from 'react'
import Typography from '@material-ui/core/Typography'; 
import InputBase from '@material-ui/core/InputBase';
import InputLabel from '@material-ui/core/InputLabel';
import TextField from '@material-ui/core/TextField';
import FormControl from '@material-ui/core/FormControl';
import { green } from '@material-ui/core/colors';
import Button from '@material-ui/core/Button';
import {
  fade,
  ThemeProvider,
  withStyles,
  makeStyles,
  createMuiTheme,
} from '@material-ui/core/styles';
import './ML.css'
import {DataContext} from '../../Context/Context'
import axios from 'axios'

const CssTextField = withStyles({
  root: {
  	color: '#084c61',
    '& label.Mui-focused': {
      color: '#084c61',
    },
    '& label':{
    	color: '#084c61'
    },
    '& .MuiInput-underline:after': {
      borderBottomColor: '#084c61',
    },
    '& .MuiOutlinedInput-root': {
      '& fieldset': {
        borderColor: '#084c61',
        borderWidth:'2px'
      },
      '&:hover fieldset': {
        borderColor: '#084c61',
      },
      '&.Mui-focused fieldset': {
        borderColor: '#084c61',
      },
    },
  },
})(TextField);

const useStyles = makeStyles((theme) => ({
  root: {
    '& > *': {
      margin: theme.spacing(1),
    },
  },
  input: {
    display: 'none',
  },
  inputField:{
  	color:'#BCE7FD',
	fontSize:'14px',
	marginTop:'15px'
  },
  button:{
  	backgroundColor:'#084c61',
  	color:'#BCE7FD',
  	'&:hover':{
  		backgroundColor:'#084c61',
  		color:'#BCE7FD',
  	}
  },
  fontColor:{
  	color:'#BCE7FD'
  }
}));


let ML = () => {

  let [prediction,setPrediction] = useState(null) 
  let [features,setFeatures] = useState([])

  let {data} = useContext(DataContext)

	const classes = useStyles();

	let getPrediction = () => {
    axios({
      method:'post',
      url:'/predict',
      withCredentials:true,
      headers: {
        'Access-Control-Allow-Origin': '*', 
        "Content-Type": "application/json" 
      },
      data:{
        features:JSON.parse(features)
      }
    })
    .then(res=>{
      setPrediction(res.data.prediction)
    })
	}

	return(
		<div className='tabElement'>
			<Typography variant='h2' gutterBottom>Machine Learning</Typography>
			<Typography variant='h7'>We tried different Machinne Learning Models for this
			problem and the best one is: <b>{data.model_info.best_model_name}</b> with test accuracy of {data.model_info.accuracy}</Typography>

			<div style={{
				display:'flex',
				marginTop:'30px',
				justifyContent:'space-between'
			}}>
				<CssTextField
        onChange={e=>{
          setFeatures(e.target.value)
        }}
				defaultValue="e.g [3,200,3.5,6]"
				className='testtubeinput'
				style={{
					width:'70%'
				}} 
				variant='outlined' label='Features'/>
				<Button
				variant="contained"
				style={{ borderRadius: 50 }}
				className='testtubebtn' 
				style={{
					width:'15%'
				}} 
				onClick={getPrediction} variant="contained" className={classes.button}>
				   Predict
				</Button>
				<div 
				style={{
					width:'15%',
					display:'flex',
					alignItems:'center',
					justifyContent:'center',
					borderRadius:'0 30px 30px 0',
					border:'solid 2px #084c61',
					paddingRight:'5px'
				}}>{prediction}</div> 
			</div>

		</div>	
		)
}	

export default ML