import React from 'react'
import PropTypes from 'prop-types';
import SwipeableViews from 'react-swipeable-views'
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import { makeStyles,useTheme } from '@material-ui/core/styles';

import Stats from '../Stats/Stats'
import DataDisplay from '../DataDisplay/DataDisplay'
import FeatureVsTarget from '../FeatureVsTarget/FeatureVsTarget'
import FeatureImportance from '../FeatureImportance/FeatureImportance'
import ML from '../ML/ML'

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`scrollable-auto-tabpanel-${index}`}
      aria-labelledby={`scrollable-auto-tab-${index}`}
      style={{
      	overflowY:'auto'
      }}
      {...other}
    >
      {value === index && (
        <Box p={3}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function a11yProps(index) {
  return {
    id: `scrollable-auto-tab-${index}`,
    'aria-controls': `scrollable-auto-tabpanel-${index}`,
  };
}

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    width: '100%',
    backgroundColor: theme.palette.background.paper,
  },
}));

let Tabbed = () => {

	const classes = useStyles();
	let theme = useTheme()
   const [value, setValue] = React.useState(0);

   const handleChange = (event, newValue) => {
     setValue(newValue);
   };

   let handleChangeIndex = index => {
   	setValue(index)
   }

	return(
		<div className={classes.root}>
      <AppBar position="sticky" color="default">
        	<Tabs
        		TabIndicatorProps={{style: {backgroundColor: '#084c61',color:'#fff'}}}
        		inkBarStyle={{backgroundColor: '#084c61'}}
        		style={{
        			background:'#680E4B',
        			color:'#C492B1'
        		}}
	         value={value}
	         onChange={handleChange}
	         variant="fullWidth"
	         scrollButtons="auto"
	         aria-label="scrollable auto tabs example"
	         centered
        	>
        		<Tab label="The Dataset" {...a11yProps(0)} />
	         <Tab label="Dataset Stats" {...a11yProps(1)} />
	         <Tab label="Features vs Target" {...a11yProps(2)} />
	         <Tab label="Feature Importances" {...a11yProps(3)} />
	         <Tab label="Machine Learning" {...a11yProps(4)} />
        	</Tabs>
      </AppBar>
      <SwipeableViews
	      axis = {theme.direction === 'rt1' ? 'x-reverse' : 'x'}
	      index = {value}
	      onChangeIndex = {handleChangeIndex}
      >
	      <TabPanel value={value} index={0}>
	      	<DataDisplay/>
	      </TabPanel>
	      <TabPanel value={value} index={1}>
	        <Stats/>
	      </TabPanel>
	      <TabPanel value={value} index={2}>
	        <FeatureVsTarget/>
	      </TabPanel>
	      <TabPanel value={value} index={3}>
	        <FeatureImportance/>
	      </TabPanel>
	      <TabPanel value={value} index={4}>
	        <ML/>
	      </TabPanel>
	   </SwipeableViews>   
    	</div>
		)
}

export default Tabbed