import React,{useContext} from 'react'
import { Line } from 'react-chartjs-2';
import Typography from '@material-ui/core/Typography';
import {
  fade,
  ThemeProvider,
  withStyles,
  makeStyles,
  createMuiTheme,
} from '@material-ui/core/styles';
import InputBase from '@material-ui/core/InputBase';
import InputLabel from '@material-ui/core/InputLabel';
import TextField from '@material-ui/core/TextField';
import FormControl from '@material-ui/core/FormControl';
import { green } from '@material-ui/core/colors';
import {DataContext} from '../../Context/Context'

const CssTextField = withStyles({
  root: {
    '& label.Mui-focused': {
      color: 'green',
    },
    '& .MuiInput-underline:after': {
      borderBottomColor: 'green',
    },
    '& .MuiOutlinedInput-root': {
      '& fieldset': {
        borderColor: 'red',
      },
      '&:hover fieldset': {
        borderColor: 'yellow',
      },
      '&.Mui-focused fieldset': {
        borderColor: 'green',
      },
    },
  },
})(TextField);

let FeaturesVsTarget = () => {

	let {data} = useContext(DataContext)

	const dataChart = {
	  labels: data.plotData.x,
	  datasets: [
	    {
	      label: 'My First dataset',
	      fill: false,
	      lineTension: 0.1,
	      backgroundColor: '#084c61',
	      borderColor: '#BCE7FD',
	      borderCapStyle: 'butt',
	      borderDash: [],
	      borderDashOffset: 0.0,
	      borderJoinStyle: 'miter',
	      pointBorderColor: 'rgba(75,192,192,1)',
	      pointBackgroundColor: '#084c61',
	      pointBorderWidth: 1,
	      pointHoverRadius: 5,
	      pointHoverBackgroundColor: 'rgba(75,192,192,1)',
	      pointHoverBorderColor: 'rgba(220,220,220,1)',
	      pointHoverBorderWidth: 2,
	      pointRadius: 1,
	      pointHitRadius: 10,
	      data: data.plotData.xy
	    }
	  ]
	} 
	let options = {
		legend: {
             labels: {
                  fontColor: '#BCE7FD'
                 }
              },
        title: {
            display: true,
            fontColor: '#BCE7FD',
            text: 'Custom Chart Title'
        }     ,
        scales: {
            yAxes: [{
                ticks: {
                    beginAtZero:true,
                    fontColor: '#BCE7FD'
                },
            }],
          xAxes: [{
                ticks: {
                    fontColor: '#BCE7FD'
                },
            }]
        } 
	}  

	return(
		<div className='tabElement'>
			<Typography variant='h2' gutterBottom>
				Features* vs Target
			</Typography>
			<Typography variant='h7' style={{
				color:'gray'
			}}>
				* Compressed using a decomposition method like PCA
			</Typography>
			<div style={{
				marginTop:'30px',
				backgroundColor:'#084c61',
				color:'white',
				borderRadius:'10px',
				padding:'20px'
			}}>
				<Line style={{
					backgroundColor:'#084c61'
				}} 
				data={dataChart}
				options={options}/>
			</div>
		</div>
		)
}

export default FeaturesVsTarget